.Overview {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  background-color: #fff;
}
.Card {
  /* width: 350px; */
  margin: 0;
  padding: 10px 40px;
  /* margin: 10px; */
  /* border: 1px dotted rgba(0,0,0,.2) */
}
.Card p {
  color: black;
  margin: 0;
  font-family: 'Montserrat', sans-serif;

}
.path-link {
  text-decoration: none !important;
}
.path-link p {
  padding-top: 20px;
  padding-bottom: 20px;
  color: black;
  font-weight: bolder;
}
.Card h1, .Card h4, .Card p {
  color: black;
  text-align: left;
}

@media only screen and (max-width: 749px) {
  .Overview  {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }
  .Card h1 {
    margin-top: 5px;
    font-size: 32px;
  }
  .path-link p {
    padding: 10px 0;
  }
}



